    .signupForm {
        .col-lg-6, .col-lg-12 {
            padding: 0 12px !important;
        }
    }

    .vue-treeselect__placeholder {
        line-height: 45px;
    }

    .signupSuccessDialog {
        text-align: center;

        .headline {
            text-align: center;
            margin-bottom: 20px;
            word-break: keep-all;
        }

        i {
            font-size: 70px;
            margin-bottom: 20px;
            color: green;
        }
